.divider {
    display: block;
    /* height: ; */
    border: 0;
    border-top: 1vmin solid #E9A822;
    /* width: 90vw; */
    margin: 1em 0;
    padding-top: 3vh;
    padding-bottom: 3vh;
    float: center;
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
}

a.customLink {
    position: relative;
    text-decoration: none;
    color: #E9A822;
}

a.customLink:before {
    content: "";
    position: absolute;
    width: 0;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #E9A822;
    color: white;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}

a.customLink:hover:before {
    color: white;
    visibility: visible;
    width: 100%;
}

a.customLink:visited {
    color: #E9A822;
}

button {
    position: absolute;
    background: none;
    border: 1px solid #E9A822;
    border-radius: 1px;
    padding: 3.5vh 3vh 3.5vh 3vh;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    background-color: transparent;
    z-index: 0;
    white-space: nowrap;
    outline: none;
    left: 50%;
    margin-top: 10vh;
    transform: translate(-50%, -50%);
}
* {
  margin: 0;
  padding: 0;
  border: none;
  background-color: #0A192F;
}

.sticky-navbar {
  background-color: #0A192F;
  position: -webkit-sticky;
  /* Safari */
  position: sticky;
  top: 0;
  height: 8vmin;
  width: 100%;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 3vmin;
  text-align: right;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  vertical-align: center;
  padding: 0;
  z-index: 1;
  /* line-height: 0; */
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  float: right;
  height: 8vmin;
  background-color: rgb(37,41,52);
  color: white;
}

li {
  float: left;
  color: white;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 3vmin 3vmin 3vmin;
  text-decoration: none;
  text-align: center;
  height: 8vmin;
}

/* Change the link color to #111 (black) on hover */

li a:hover {
  background-color: #E9A822;
  color: white;
}

li a:visited {
  color: white;
}
